'use client';
import React, { useEffect, useState } from 'react';
import { StatCard } from '@/components/admin/dashboard/StatCard';
import { AttentionWidget } from '@/components/admin/dashboard/AttentionWidget';
import { ActivityFeed } from '@/components/admin/dashboard/ActivityFeed';
import { DollarSign, Users, Calendar, TrendingUp } from 'lucide-react';

export default function AdminDashboardPage() {
    const [data, setData] = useState<any>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function fetchData() {
            try {
                const res = await fetch('/api/admin/dashboard');
                if (res.ok) {
                    setData(await res.json());
                }
            } catch (e) {
                console.error(e);
            } finally {
                setLoading(false);
            }
        }
        fetchData();
    }, []);

    if (loading) return <div className="p-8">Loading dashboard...</div>;

    const { stats, alerts } = data || { stats: {}, alerts: [] };

    return (
        <div className="min-h-screen bg-slate-50/50 p-6 space-y-8">

            {/* 1. Header & Quick Actions */}
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-slate-900">Mission Control</h1>
                    <p className="text-slate-500">
                        System is running. {alerts?.length > 0 ? `${alerts.length} items require attention.` : 'All clear.'}
                    </p>
                </div>
                <div className="flex gap-2">
                    <button
                        onClick={() => window.location.reload()}
                        className="px-4 py-2 bg-white border border-slate-200 text-slate-700 font-medium rounded-lg shadow-sm hover:bg-slate-50 transition-colors"
                    >
                        Refresh Data
                    </button>
                    <button className="px-4 py-2 bg-slate-900 text-white font-medium rounded-lg shadow-sm hover:bg-slate-800 transition-colors">
                        + New Booking
                    </button>
                </div>
            </div>

            {/* 2. System Overview (Top Row) */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                <StatCard
                    title="Revenue Today"
                    value={`$${stats?.revenueToday || 0}`}
                    trend="+12%"
                    trendDirection="up"
                    icon={<DollarSign className="w-5 h-5 text-emerald-600" />}
                />
                <StatCard
                    title="Bookings"
                    value={stats?.bookingsToday?.toString() || "0"}
                    trend="+5%"
                    trendDirection="up"
                    icon={<Calendar className="w-5 h-5 text-blue-600" />}
                />
                <StatCard
                    title="Active Users"
                    value={stats?.activeUsers?.toString() || "0"}
                    trend="Stable"
                    trendDirection="neutral"
                    icon={<Users className="w-5 h-5 text-violet-600" />}
                />
                <StatCard
                    title="Conversion"
                    value={`${stats?.conversionRate || 0}%`}
                    trend="-0.4%"
                    trendDirection="down"
                    icon={<TrendingUp className="w-5 h-5 text-amber-600" />}
                />
            </div>

            {/* 3. Main Split (Mission Control + Feed) */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 h-[600px]">
                {/* Mission Control (2/3 width) */}
                <div className="lg:col-span-2 h-full">
                    {/* Pass alerts prop if AttentionWidget supports it, otherwise use default for now. 
                Ideally refactor AttentionWidget to accept props.
                For this MVP, I'll pass nothing as I haven't updated AttentionWidget props yet, 
                BUT I should update AttentionWidget next. 
            */}
                    <AttentionWidget alerts={alerts} />
                </div>

                {/* Sidebar Feed (1/3 width) */}
                <div className="h-full">
                    <ActivityFeed />
                </div>
            </div>

        </div>
    );
}
