import React from 'react';

export const ActivityFeed = () => {
    const activities = [
        { id: 1, user: 'John D.', action: 'booked', target: 'Desert Safari', time: '2m ago' },
        { id: 2, user: 'Marketing', action: 'sent', target: 'Newsletter Campaign', time: '15m ago' },
        { id: 3, user: 'System', action: 'updated', target: 'Exchange Rates', time: '1h ago' },
        { id: 4, user: 'Ali (Guide)', action: 'completed', target: 'Medina Tour', time: '1h ago' },
    ];

    return (
        <div className="bg-white rounded-2xl border border-slate-100 shadow-sm p-6 h-full">
            <h3 className="font-semibold text-slate-900 mb-4">Live Activity</h3>
            <div className="space-y-6 relative before:absolute before:inset-0 before:ml-2.5 before:-translate-x-px md:before:mx-auto md:before:translate-x-0 before:h-full before:w-0.5 before:bg-gradient-to-b before:from-transparent before:via-slate-200 before:to-transparent">
                {activities.map((item) => (
                    <div key={item.id} className="relative flex gap-4">
                        <div className="absolute left-0 mt-1.5 ml-1.5 -translate-x-1/2 w-2 h-2 rounded-full border border-white bg-slate-300 ring-4 ring-white"></div>
                        <div className="pl-4">
                            <p className="text-sm text-slate-600">
                                <span className="font-medium text-slate-900">{item.user}</span> {item.action} <span className="font-medium text-slate-900">{item.target}</span>
                            </p>
                            <div className="text-xs text-slate-400 mt-0.5">{item.time}</div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};
